import React from "react";
import { useRef } from "react";
import goodsService from "../../services/goods.services";
import TextField from "./../../components/Form/TextField/textField";

const convertBase64 = (file) => {
  return new Promise((resolve, reject) => {
    const fileReader = new FileReader();
    fileReader.readAsDataURL(file);

    fileReader.onload = () => {
      resolve(fileReader.result);
    };

    fileReader.onerror = (error) => {
      reject(error);
    };
  });
};

const Admin = () => {
  const goods = useRef({
    id: "",
    name: "",
    category: [],
    description: "",
    price: 0,
    img: {},
  });

  async function createGoods(data) {
    try {
      goodsService.create(data);
    } catch (error) {
      console.log("expected error");
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    createGoods(goods.current);
  };

  const handleChange = (key) => (e) => {
    const { target: value } = e;
    const newValue = value.value;

    if (key === "category") {
      goods.current[key] = [newValue];
      return;
    }

    if (key === "price") {
      goods.current[key] = Number(newValue);
      return;
    }

    if (key === "img") {
      convertBase64(e.target.files[0]).then((file) => {
        goods.current[key] = file;
      });
      return;
    }

    goods.current[key] = newValue;
  };

  return (
    <div className="container mt-5">
      <div className="row">
        <div className="col-md-6 offset-md-3 shadow p-4">
          <h4 className="mb-4">   </h4>
          <form onSubmit={handleSubmit}>
            <TextField
              label=" id :"
              type="text"
              onChange={handleChange("id")}
              name="id"
            />
            <TextField
              label=" :"
              type="text"
              onChange={handleChange("name")}
            />
            <div className="mb-4">
              <label htmlFor="category" className="form-label">
                :
              </label>
              <div className="input-group ">
                <select
                  id="category"
                  className="basic-multi-select"
                  onChange={handleChange("category")}
                >
                  <option disabled selected>
                     
                  </option>
                  <option value={"cat1"}> </option>
                  <option value={"cat2"}> </option>
                  <option value={"cat3"}> </option>
                  <option value={"cat4"}></option>
                </select>
              </div>
            </div>
            <TextField
              label=" :"
              type="number"
              onChange={handleChange("price")}
            />

            <label htmlFor="photo" className="form-label">
               :
            </label>
            <div className="input-group mb-3">
              <input type="file" onChange={handleChange("img")}></input>
            </div>

            <TextField
              label=" :"
              type="text"
              onChange={handleChange("description")}
            />
            <button type="submit" className="btn btn-primary w-100 mx-auto">
               
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Admin;
